            margin: .25em .25em 0 0;
            color: @color-primary;
            background: @gainsboro-light;

        }

        li.pagination-start {

        }

        li.pagination-prev {

        }

        li.pagination-next {

        }

        li.pagination-end {

        }
    }
}

