// flat allg stil
.flat {
    float: right !important;
    margin: 0 0 .2em 0 !important;
}

a.flat {
    color: @concrete-darker;
    background: @gainsboro-light;
    .border-radius(@baseBorderRadius);
    padding: .5em 1em;
    display: block;
    font-size: 87.5%;
    font-weight: normal;
    text-decoration: none;
    cursor: pointer;
    line-height: 1.4;

    &:hover {
        color: @whitesmoke;
        background: @concrete;
    }

    &:focus, &:active {
        color: @whitesmoke;
        background: @concrete-dark;
    }
}

// pager / pagenav overrides
ul.pager, ul.pagenav {
    .make-block;
    margin: .5em 0;
    padding: 0;
    font-size: 0.875em;

    li {
        list-style-type: none;

        &.previous {
            float: left;
        }

        &.next {
            float: right;
        }
    }

    li a {
        display: inline-block;
        .ie7-inline-block();
        padding: .5em 1em .33em 1em;
        margin: 0;
        font-size: @font-size-default;
        line-height: @line-height-default;
        text-align: center;
        vertical-align: middle;
        cursor: pointer;
        border: 0;
        *border: 0; // Remove the border to prevent IE7's black border on input:focus
        .border-radius(0);
        .ie7-restore-left-whitespace(); // Give IE7 some love
        .box-shadows(0 0 0 0 transparent, 0 0 0 0 transparent);
        text-shadow: none;
        -webkit-transition: all 0.3s ease-out;
        transition: all 0.3s ease-out;

        // Hover/focus state
        &:hover,
        &:focus {
            text-decoration: none;
            text-shadow: none;
            outline: 0;
            .box-shadows(0 8px 17px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19));
        }

        // Focus state for keyboard and accessibility
        &:focus {
            .tab-focus();
            outline: 0;
        }

        // Active state
        &.active,
        &:active {
            background-image: none;
            outline: 0;
            .box-shadows(0 2px 5px 0 rgba(0, 0, 0, 0.16), 0 2px 10px 0 rgba(0, 0, 0, 0.12));
            text-shadow: none;
        }
    }
}

